import axios from 'axios';

export default class PixabayApi { //   PixabayApi   loadImages()  loadNextImages()
  //    

  #API_KEY = '34734922-71a756c5ae22b2ca14df3cfaf'; //  API
  #URL = 'https://pixabay.com/api/'; // URL API     
  #PER_PAGE = 40; //    

  #page = 1; // 
  #query = ''; //  ,          loadImages()
  //    ?       ,      
  //        loadImages()  loadNextImages()
  loadImages(query) {
    this.#page = 1; //       
    this.#query = query.trim(); //      

    return this.#fetch(); //    #fetch()    
  }

  loadNextImages() { //     
    this.#page += 1; //    1

    return this.#fetch(); //    #fetch()    
  }

  async #fetch() { //   #fetch()   API 
    const response = await axios({  //  axios     '  
      url: this.#URL, // URL API
      params: { //  
        key: this.#API_KEY,
        q: this.#query, 
        image_type: 'photo', 
        orientation: 'horizontal', 
        safesearch: true, 
        per_page: this.#PER_PAGE, 
        page: this.#page, 
      },
    }); 

    return response.data; //    response.data     API  
    //     loadImages()  loadNextImages()  
  }
}
