import './css/styles.css';
import 'simplelightbox/dist/simple-lightbox.min.css';
import SimpleLightbox from 'simplelightbox';
import PixabayApi from './js/pixabayAPI';
import GalleryCardsBuilder from './js/galleryCardsBuilder';
import { Notify } from 'notiflix';

const refs = {
  form: document.querySelector('.search-form'),
  input: document.querySelector('.search-form__input'),
  button: document.querySelector('.search-form__button'),
  gallery: document.querySelector('.gallery'),
  loader: document.querySelector('.loader'),
};
//     PixabayApi,     pixabayAPI.js
//       PixabayApi   pixabay
//     PixabayApi      
//  pixabay.loadImages(query) -    PixabayApi     
const pixabay = new PixabayApi();
//     GalleryCardsBuilder,     galleryCardsBuilder.js
//       GalleryCardsBuilder   builder
//     GalleryCardsBuilder      
//  builder.buildCards(data.hits) -    GalleryCardsBuilder    
const builder = new GalleryCardsBuilder();
//     SimpleLightbox,     simple-lightbox.js
//       SimpleLightbox   lightbox
//     SimpleLightbox      
//  lightbox.refresh() -    SimpleLightbox   
//           
//          
//         
//         
const lightbox = new SimpleLightbox('.gallery a', {
  captionsData: 'alt',
  captionPosition: 'bottom',
  captionDelay: 250,
});
//     IntersectionObserver,      JavaScript
//          
//       IntersectionObserver   scrollObserver
//     IntersectionObserver      
//  scrollObserver.observe(refs.loader) -    IntersectionObserver 
//        
//          callback- 
//     IntersectionObserver
//      function (entries) {...}
//      ' entries
//        '
//   '           
//        loader
//  isIntersecting  '           
//   isIntersecting  '   true       
//   isIntersecting  '   false        
const scrollObserver = new IntersectionObserver(
  function (entries) {
    // callback-         
    // console.log(entries); //    '
    entries.forEach(entry => {
      //     '        '
      if (entry.isIntersecting) {
        //           loadMoreImages
        loadMoreImages();
      }
    });
  },
  //  '     IntersectionObserver
  //  rootMargin  '      
  //      :
  //   0px,   0px,   300px,   0px
  //     0.1,     10%       
  //     
  {
    rootMargin: '0px 0px 300px 0px',
    threshold: 0.1,
  }
);
//      IntersectionObserver   
// https://developer.mozilla.org/en-US/docs/Web/API/Intersection_Observer_API
//   https://www.youtube.com/watch?v=ZYqBZmU-tA0&ab_channel=%D0%9C%D0%B8%D1%85%D0%B0%D0%B8%D0%BB%D0%9D%D0%B5%D0%BF%D0%BE%D0%BC%D0%BD%D1%8F%D1%89%D0%B8%D0%B9
//     https://www.smashingmagazine.com/2018/01/deferring-lazy-loading-intersection-observer-api/
//   https://goit.global/javascript/ru/v1/module-08/intersection-observer.html

let inputEmpty = true; //          
let showedImages = 0; //        
let totalHits = 0; //        

refs.form.addEventListener('submit', onFormSubmit);
refs.input.addEventListener('input', onInput);
refs.button.addEventListener('click', onButtonClick);
refs.button.addEventListener('mouseleave', onButtonMouseLeave);
//    mouseleave  
//              
//       

//     onInput         
//       
//          disabled  
//    
function onInput(event) {
  if (event.target.value.trim()) {
    inputEmpty = false;
    //        inputEmpty  false,       
    refs.button.removeAttribute('disabled'); //   disabled   
  } else {
    inputEmpty = true; //     inputEmpty  true,        
    refs.button.setAttribute('disabled', 'disabled'); //   disabled   
    // ,   disabled       onFormSubmit
    //         html
  }
}
//          onButtonClick, onButtonMouseLeave, unfocusButton
function onButtonClick() {
  refs.button.classList.add('search-form__button--focus');
}

function onButtonMouseLeave() {
  unfocusButton();
}

function unfocusButton() {
  refs.button.classList.remove('search-form__button--focus');
}

//     onFormSubmit       

function onFormSubmit(event) {
  event.preventDefault();
  //     
  if (inputEmpty) {
    return;
  }

  stopObserver(); //   stopObserver      
  //  gallery   innerHTML    
  refs.gallery.innerHTML = '';
  showedImages = 0; //         
  totalHits = 0; //         
  //              
  //    query      
  const query = event.target.elements.searchQuery.value;
  //   event.target.elements.searchQuery.value      
  //     (       )
  // console.log(query);
  //    loadImages   query
  //     
  loadImages(query);
}
//      await           
async function loadImages(query) {
  try {
    const data = await pixabay.loadImages(query); //   loadImages   query

    if (data.totalHits === 0) {
      //      
      Notify.failure(
        //   Notify.failure   'Sorry  ..'
        'Sorry, there are no images matching your search query. Please try again.'
      );

      return; //   
    }

    totalHits = data.totalHits; //    totalHits     
    //   Notify.success       
    //    totalHits    
    Notify.success(`'Hooray! We found ${totalHits} images.'`);
    //     buildGallery   data
    //     
    buildGallery(data);
    //   lightbox      refresh
    //   
    lightbox.refresh();
    //     unfocusButton    search-form__button--focus
    setTimeout(unfocusButton, 500);
    //  showedImages      
    //       
    showedImages = data.hits.length;

    if (showedImages < totalHits) {
      //         
      refs.loader.classList.remove('loader--hidden'); //   loader--hidden   loader
      //    startObserver      
      //    loadMoreImages   
      //      
      startObserver();
    }
  } catch (error) {
    console.error(error); //       
  }
}
//     loadMoreImages            
//   
//     
async function loadMoreImages() {
  try {
    const data = await pixabay.loadNextImages(); //   loadNextImages

    if (showedImages === totalHits) {
      //        
      //   loader--hidden   loader
      refs.loader.classList.add('loader--hidden');
      //    stopObserver   
      stopObserver();
      //       
      Notify.info("We're sorry, but you've reached the end of search results.");
    }
    //   
    //   appendGallery   data
    //      
    appendGallery(data);
    //    lightbox
    lightbox.refresh();
    //    showedImages      
    showedImages += data.hits.length;
  } catch (error) {
    console.error(error);
  }
}
//  buildGallery   buildCards   data.hits -  
function buildGallery(data) {
  const markup = builder.buildCards(data.hits);
  //      gallery     
  refs.gallery.innerHTML = markup;
}
//  appendGallery   buildCards   data.hits -  
//       gallery
function appendGallery(data) {
  const markup = builder.buildCards(data.hits);
  //    insertAdjacentHTML      - 
  //            innerHTML
  refs.gallery.insertAdjacentHTML('beforeend', markup);
}
//    startObserver      
//    loadMoreImages   
//      
//       observe
//        
//      loader
// scrollObserver.observe(refs.loader) -    observe   refs.loader;
// scrollObserver -      
// observe -    IntersectionObserver     
function startObserver() {
  setTimeout(function () {
    //  setTimeout      
    scrollObserver.observe(refs.loader); //   observe   refs.loader
  }, 1000);
}

function stopObserver() {
  //  stopObserver   unobserve   refs.loader
  scrollObserver.unobserve(refs.loader); //    
}
